import React,{Component} from 'react';
import PropTypes from 'prop-types'

import {Form,Input,Tree} from 'antd'

import menuList from '../../../../config/menuConfig'

const {TreeNode} =Tree

export default class AuthMenuForm extends Component{
    constructor(props){
        super(props)
        
        //
        const {menu}=this.props.group
        this.state={
            checkedKeys:menu?menu.split(','):menu,
        }
    }
    static propTypes={
        group:PropTypes.object
    }

    //
    getMenus=()=>this.state.checkedKeys
    
    //
    getTreeNodes=list=>{
        return list.reduce((pre,item)=>{
            pre.push(
                <TreeNode title={item.title} key={item.key}>
                    {item.children ? this.getTreeNodes(item.children):null}
                </TreeNode>
            )
            return pre
        },[])
    }
    
    //nodes
    onCheck=(checkedKeys)=>{
        this.setState({
            checkedKeys
        })
    }
    
    componentWillMount(){
        this.treeNodes=this.getTreeNodes(menuList)
    }
    
    //
    componentWillReceiveProps(nextProps){//group
        const menus=nextProps.group.menu
        this.setState({
            checkedKeys:menus?menus.split(','):menus
        })
    }
    
    render(){
        const {group}=this.props
        const {checkedKeys}=this.state
        const formItemLayout={
            labelCol:{span:5},
            wrapperCol:{span:15}
        }
        return(
            <div>
                <Form.Item label='' {...formItemLayout}>
                    <Input value={group.name} disabled/>
                </Form.Item>
                <Tree
                    checkable
                    defaultExpandAll={true}
                    checkedKeys={checkedKeys}
                    onCheck={this.onCheck}
                >
                    {this.treeNodes}
                </Tree>
            </div>
        )
    }
}