import React,{Component} from 'react';
import {Card,Button,Modal,Icon,Table,message,Collapse} from 'antd'


import EditBtn from '../../../../components/editbtn'
import DeleteBtn from '../../../../components/deletebtn'
import PreviewBtn from '../../../../components/previewbtn'
import {shortDate} from '../../../../utils/dateUtils'
import {PAGE_SIZE} from '../../../../utils/constants'
import {rBooks,dBook} from '../../../../api'
import SearchForm from './searchform'

const { Panel } = Collapse

export default class Home extends Component{
    state={
        books:[],
        total:0,
        loading:false,
        isShow:false,
        bookcategory:'',  //
        name:'',
        author:'',
        publisher:'',
        publisheryear:'',

    }
    
    initColums=()=>{
        this.columns=[{
            title:'',
            dataIndex:'bookcategory',
            width: 150,
            render:(bookcategory)=>{return(bookcategory.name)}
        },{
            title:'',
            dataIndex:'name',
            width: 250,
        },{
            title:'isbn',
            dataIndex:'isbn',
            width: 150,
        },{
            title:'',
            width: 100,
            dataIndex:'author',
        },{
            title:'',
            width: 200,
            dataIndex:'publisher',
        },{
            title:'',
            width: 80,
            dataIndex:'publisheryear',
        },{
            title:'',
            width: 80,
            dataIndex:'price',
        },{
            title:'',
            dataIndex:'create_time',
            width: 100,
            render:(create_time)=>shortDate(create_time)
        },{
            title:'',
            fixed: 'right',
            width: 100,
            render:(book)=>(
            <span>
                <PreviewBtn onClick={()=>this.props.history.push('/book/detail',{book})}/>&nbsp;&nbsp;&nbsp;
                <EditBtn onClick={()=>this.props.history.push('/book/addorupdate',book)}/>&nbsp;&nbsp;&nbsp;
                <DeleteBtn onClick={()=>this.deleteBook(book)}/>
            </span>
            )
        }]
    }
    getBooks= async pageNum=>{
        this.pageNum=pageNum
        const isPage=true
        this.setState({loading:true})
        const{bookcategory,name,author,publisher,publisheryear}=this.state
        let result = await rBooks({
            isPage,
            pageNum,
            pageSize:PAGE_SIZE,
            bookcategory,
            name,
            author,
            publisher,
            publisheryear,
        })
        this.setState({loading:false})
        if(result.status===1){
            const {total,list}=result.data
            this.setState({books:list,total})
        }else{
            this.setState({books:[]})
            message.error(result.msg)
        }
    }

    showAdd=()=>{
        this.book=null
        this.setState({isShow:true})
    }
    
    showUpdate=(book)=>{
        this.book=book
        this.setState({isShow:true})
    }

    resetForm=()=>{
        this.form.resetFields()
    }
    
    deleteBook=(book)=>{
        Modal.confirm({
            title:'',
            onOk:async()=>{
                const result=await dBook(book._id)
                if(result.status===1){
                    message.success('')
                    this.getBooks(1)
                }
            }
        })
    }
    
    setSearchItem=(searchItem)=>{
        this.setState({
            bookcategory:searchItem.bookcategory,
            name:searchItem.name,
            author:searchItem.author,
            publisher:searchItem.publisher,
            publisheryear:searchItem.publisheryear,
        },()=>{  //setState
            this.getBooks(this.pageNum)
        })
    }

    componentWillMount(){
        this.initColums()
    }
    componentDidMount(){
        this.getBooks(1)
    }
    
    render(){
        const {books,total,loading}=this.state
        const title=(<span>
            <Button type='primary' onClick={()=>this.props.history.push('/book/addorupdate')}><Icon type='unordered-list'/></Button>
            </span>)
 
        return(
            <Card title={title} >
                <Collapse>
                <Panel header="" >
                <SearchForm setForm={(form)=>{this.form=form}} setSearchItem={this.setSearchItem}/>
                </Panel>
                </Collapse>
                <Table
                bordered
                size='small'
                rowKey='_id'
                loading={loading}
                dataSource={books}
                columns={this.columns}
                scroll={{ x: 1000 }}
                pagination={{
                    current:this.pageNum,
                    defaultPageSize:PAGE_SIZE,
                    ShowQuickJumper:true,
                    total,
                    onChange:this.getBooks
                    }}
                />
            </Card>
        )
    }
}