import {
    GET_DEVICECATEGORY,
    COU_DEVICECATEGORY,
    DEL_DEVICECATEGORY,
    GET_DEVICEMODEL,
    COU_DEVICEMODEL,
    DEL_DEVICEMODEL,
    GET_INSTALLLOCATION,
    COU_INSTALLLOCATION,
    DEL_INSTALLLOCATION,
    GET_DEVICEINFO,
    COU_DEVICEINFO,
    DEL_DEVICEINFO
} from '../actiontypes'

//
export const devicecategoryReducer=(state={},action)=>{
    switch(action.type){
        case GET_DEVICECATEGORY:
            return {
                ...state,
                data:action.data
            }
        case COU_DEVICECATEGORY:
            return {
                ...state,
                msg:action.msg,
                status:action.status
            }
        case DEL_DEVICECATEGORY:
            return {
                ...state,
                msg:action.msg,
                status:action.status
            }
        default:
            return state
    }
}
