import React,{Component} from 'react';
import {connect} from 'react-redux'
import {Card,Table,Button,Icon,message,Modal} from 'antd'
import PropTypes from 'prop-types'

import EditBtn from '../../../../components/editbtn'
import DeleteBtn from '../../../../components/deletebtn'
import {PAGE_SIZE} from '../../../../utils/constants'
import {rGro,couGro,dGro} from '../../../../redux/actions/account-action'
import AddForm from './addform'
import AuthMenuForm from './authmenuform'
import AuthOperationForm from './authoperationform'
import {formateDate} from '../../../../utils/dateUtils'


class Group extends Component{
    constructor(props){
        super(props)
        this.auth=React.createRef()
        this.state={
            isShowAdd:false,
            isShowGM:false,
            isShowGO:false,
            loading:false,
            groups:[],  //,table
            selectedGroup:{},    //,
        }
    }

    initColums=()=>{
        this.columns=[
        {
            title:'',
            dataIndex:'name',
        },
        {
            title:'',
            dataIndex:'create_time',
            render:(create_time)=>formateDate(create_time)
        },
        {
            title:'',
            width:300,
            render:(group)=>(
            <span>
                <EditBtn onClick={()=>this.showUpdate(group)}/>&nbsp;&nbsp;&nbsp;
                <DeleteBtn onClick={()=>this.deleteGroup(group)}/>
            </span>
            )
        }
        ]
    }
    initGroups=async ()=>{
        this.setState({loading:true})
        await this.props.rGro()
        const result=this.props.groupReducer
        this.setState({loading:false})
        this.setState({groups:result.data})
    }
    
    //
    onRow=(group)=>{
        return{
            onClick:event=>{
                this.setState({
                    selectedGroup:group
                })
            }
        }
    }

    showAdd=()=>{
        this.group=null
        this.setState({
            isShowAdd:true
        })
    }
    addOrUpdateGroup=()=>{
        this.form.validateFields(async(err,values)=>{
            if(!err){
                this.setState({isShowAdd:false})
                const group=values
                this.form.resetFields()
                if(this.group){
                    group._id=this.group._id
                }
                await this.props.couGro(group)
                const result=this.props.groupReducer
                if(result.status===1){
                    message.success(result.msg)
                    this.initGroups()
                }else{
                    message.error(result.msg)
                }
            }
        })
    }

    showUpdate=(group)=>{
        this.group=group
        this.setState({isShowAdd:true})
    }
    
    deleteGroup=(group)=>{
        Modal.confirm({
            title:' '+group.name+' ',
            onOk:async()=>{
                await this.props.dGro(group._id)
                const result=this.props.groupReducer
                if(result.status===1){
                    message.success(result.msg)
                    this.initGroups()
                }else{
                    message.error(result.msg)
                }
            }
        })
    } 
    showMenuAuth=()=>{
        this.setState({
            isShowGM:true
        })
    }
    showOperationAuth=()=>{
        this.setState({
            isShowGO:true
        })
    }

    GroupMenuAuth=async()=>{
        this.setState({
            isShowGM:false
        })
        const group=this.state.selectedGroup
        const menus=this.auth.current.getMenus()
        group.menu=menus?menus.join(','):menus
        await this.props.couGro(group)
        const result=this.props.groupReducer
        if(result.status===1){
            message.success(result.msg)
            this.initGroups()
        }else{
            message.error(result.msg)
        }
    }
    
    GroupOperationAuth=async()=>{
        this.setState({
            isShowGO:false
        })
        const group=this.state.selectedGroup
        const operations=this.auth.current.getOperations()
        group.operation=operations?operations.join(','):operations
        
        await this.props.couGro(group)
        const result=this.props.groupReducer
        if(result.status===1){
            //
            /*if(group._id===memUtils.user.group_id){
                memUtils.user={}
                storeUtils.removeUser()
                this.props.history.replace('/login')
                message.success('')
            }else{
                message.success('')
                this.setState({
                    groups:[...this.state.groups]
                })
            }*/
            message.success(result.msg)
            this.initGroups()
        }else{
            message.error(result.msg)
        }
    }
    
    componentWillMount(){
        this.initColums()
    }
    componentDidMount(){
        this.initGroups()
    }
    render(){
        const {groups,loading,selectedGroup,isShowAdd,isShowGM,isShowGO}=this.state
        const group=this.group||{}
        const title=(
             <span>
                 <Button type='primary' onClick={this.showAdd}><Icon type="usergroup-add" /></Button>&nbsp;&nbsp;
                 <Button type='primary' onClick={this.showMenuAuth} disabled={!selectedGroup._id}><Icon type='menu'/></Button>&nbsp;&nbsp;
                 <Button type='primary' onClick={this.showOperationAuth} disabled={!selectedGroup._id}><Icon type='tool'/></Button>
             </span>)
        return(
            <Card title={title} >
                <Table
                bordered
                rowKey='_id'
                loading={loading}
                dataSource={groups}
                columns={this.columns}
                pagination={{defaultPageSize:PAGE_SIZE}}
                rowSelection={{
                    type:'radio',
                    selectedRowKeys:[selectedGroup._id],
                    onSelect:(group)=>{
                        this.setState({
                            selectedGroup:group
                        })
                    }
                    }}
                onRow={this.onRow}
                />
                <Modal
                  title={group._id ? "" : ''}
                  visible={isShowAdd}
                  onOk={this.addOrUpdateGroup}
                  onCancel={()=>{
                      this.form.resetFields()
                      this.setState({isShowAdd:false}) 
                  }}
                >
                    <AddForm setForm={(form)=>{this.form=form}} group={group}/>
                </Modal>
                <Modal
                  title=""
                  visible={isShowGM}
                  onOk={this.GroupMenuAuth}
                  onCancel={()=>{
                      this.setState({isShowGM:false})
                  }}
                >
                    <AuthMenuForm 
                    ref={this.auth} 
                    group={selectedGroup}
                    />
                </Modal>
                <Modal
                  title=""
                  visible={isShowGO}
                  onOk={this.GroupOperationAuth}
                  onCancel={()=>{
                      this.setState({isShowGO:false})
                  }}
                >
                <AuthOperationForm ref={this.auth} group={selectedGroup}/>
                </Modal>
            </Card>
        )
    }
}

Group.propTypes={
    groupReducer:PropTypes.object.isRequired,
    rGro:PropTypes.func.isRequired,
    couGro:PropTypes.func.isRequired,
    dGro:PropTypes.func.isRequired,
}

const mapStateToProps = state => {
    return {
        groupReducer:state.groupReducer
    }
}

const mapDispatchToProps = {rGro,couGro,dGro}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Group)