import React,{Component} from 'react'
import {connect} from 'react-redux'
import {Card,Table,Button,Icon,message,Modal} from 'antd'
import PropTypes from 'prop-types'

import EditBtn from '../../../components/editbtn'
import DeleteBtn from '../../../components/deletebtn'
import {PAGE_SIZE} from '../../../utils/constants'
import {rTls,couTl,dTl} from '../../../redux/actions/timeline-action'
import AddForm from './addform'
import {formateDate,shortDate} from '../../../utils/dateUtils'

class TimeLine extends Component{

    constructor(props){
        super(props)
        this.state={
            isShowAdd:false,
            loading:false,
            timelines:[],  //,table
        }
    }

    initColums=()=>{
        this.columns=[
        {
            title:'',
            dataIndex:'name',
        },
        {
            title:'',
            dataIndex:'details',
        },
        {
            title:'',
            dataIndex:'create_time',
            render:(create_time)=>formateDate(create_time)
        },
        {
            title:'',
            width:300,
            render:(timeline)=>(
            <span>
                <EditBtn onClick={()=>this.showUpdate(timeline)}/>&nbsp;&nbsp;&nbsp;
                <DeleteBtn onClick={()=>this.deleteTimeLine(timeline)}/>
            </span>
            )
        }
        ]
    }
    getTimeLines=async ()=>{
        await this.props.rTls()
        const result=this.props.timelineReducer
        this.setState({ timelines:result.data })
    }

    showAdd=()=>{
        this.timeline=null
        this.setState({
            isShowAdd:true
        })
    }
    addOrUpdateTimeLine=()=>{
        this.form.validateFields(async(err,values)=>{
            if(!err){
                this.setState({isShowAdd:false})
                const timeline=values
                timeline.create_time=shortDate(values['create_time'])
                this.form.resetFields()
                if(this.timeline){
                    timeline._id=this.timeline._id
                }
                await this.props.couTl(timeline)
                const result=this.props.timelineReducer
                if(result.status===1){
                    message.success(result.msg)
                    this.getTimeLines()
                }else{
                    message.error(result.msg)
                }
            }
        })
    }

    showUpdate=(timeline)=>{
        this.timeline=timeline
        this.setState({isShowAdd:true})
    }
    
    deleteTimeLine=(timeline)=>{
        Modal.confirm({
            title:' '+timeline.name+' ',
            onOk:async()=>{
                await this.props.dTl(timeline._id)
                const result=this.props.timelineReducer
                if(result.status===1){
                    message.success(result.msg)
                    this.getTimeLines()
                }else{
                    message.error(result.msg)
                }
            }
        })
    } 
    
    componentWillMount(){
        this.initColums()
    }
    componentDidMount(){
        this.getTimeLines()
    }
    render(){
        const {timelines,loading,isShowAdd}=this.state
        const timeline=this.timeline||{}
        const title=(
             <span>
                 <Button type='primary' onClick={this.showAdd}><Icon type="history" /></Button>
             </span>)
        return(
            <Card title={title} >
                <Table
                bordered
                rowKey='_id'
                loading={loading}
                dataSource={timelines}
                columns={this.columns}
                pagination={{defaultPageSize:PAGE_SIZE}}
                />
                <Modal
                  title={timeline._id ? "" : ''}
                  visible={isShowAdd}
                  onOk={this.addOrUpdateTimeLine}
                  onCancel={()=>{
                      this.form.resetFields()
                      this.setState({isShowAdd:false}) 
                  }}
                >
                    <AddForm setForm={(form)=>{this.form=form}} timeline={timeline}/>
                </Modal>
            </Card>
        )
    }
}

TimeLine.propTypes={
    timelineReducer:PropTypes.object.isRequired,
    rTls:PropTypes.func.isRequired,
    couTl:PropTypes.func.isRequired,
    dTl:PropTypes.func.isRequired
}

const mapStateToProps = state => {
    return {timelineReducer:state.timelineReducer}
}

const mapDispatchToProps =  {rTls,couTl,dTl}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TimeLine)