import React,{PureComponent} from 'react';
import PropTypes from 'prop-types'

import {Form,Input,Select,Switch} from 'antd'

const Item=Form.Item
const Option=Select.Option

class AddForm extends PureComponent{
    
    static propTypes={
        setForm:PropTypes.func.isRequired,
        groups:PropTypes.array.isRequired,
        user:PropTypes.object
    }
    
    componentWillMount(){
        this.props.setForm(this.props.form)
    }
    
    render(){
        const {groups,user}=this.props
        const {getFieldDecorator}=this.props.form
        const formItemLayout={
            labelCol:{span:5},
            wrapperCol:{span:15}
        }
        return(
            <Form>
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('username',{
                        initialValue:user.username,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        user.username==='admin'?<Input disabled/>:<Input placeholder='!'/>
                    )
                }
                </Item>
                {user._id ? null:(
                    <Item label='' {...formItemLayout}>
                    {
                    getFieldDecorator('password',{
                        initialValue:user.password,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <Input placeholder='!' type="password"/>
                    )
                    }
                </Item>
                )}
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('name',{
                        initialValue:user.name,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <Input placeholder='!'/>
                    )
                }
                </Item>
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('group',{
                        initialValue:user.group,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <Select mode='multiple'>
                        {
                            groups.map(group=><Option key={group._id} value={group._id}>{group.name}</Option>)
                        }
                        </Select>
                    )
                }
                </Item>
                <Item label='' {...formItemLayout}>
                    {
                    getFieldDecorator('issuper', { 
                        valuePropName: 'checked',
                        initialValue:user.isSuper?user.isSuper:false
                    })(
                        <Switch />
                    )}
                </Item>
            </Form>
        )
    }
}
export default Form.create()(AddForm)