import React,{Component} from 'react';

import {Card,Descriptions} from 'antd'
import BackBtn from '../../../../components/backbtn'
import {deviceRunSystem,deviceStatus} from '../../../../config/selectConfig'
import {shortDate} from '../../../../utils/dateUtils'

const Item=Descriptions.Item

export default class DeviceInfoDetail extends Component{
    runosdisplay=para=>{
        let display=''
        deviceRunSystem.forEach(item=>{
            if(item.value===String(para)){
                display= item.label
            }
        })
        return display
    }
    
    statusdisplay=para=>{
        let display=''
        deviceStatus.forEach(item=>{
            if(item.value===String(para)){
                display= item.label
            }
        })
        return display
    }
    
    render(){
        const {
            devicemodel,
            parent,
            installlocation,
            runos,
            sn,
            name,
            ip,
            mac,
            status,
            remarks,
            installdate
        }=this.props.location.state.deviceinfo
        
        const title=(
            <span>
                <BackBtn onClick={()=>this.props.history.goBack()}/>
                <span></span>
            </span>
        )
        return(
            <Card title={title}>
              <Descriptions bordered>
                <Item label='' span={1}>{devicemodel.devicecategory.name}</Item>
                <Item label='' span={2}>{devicemodel.name}</Item>
                <Item label='' span={1}>{parent?parent.name:''}</Item>
                <Item label='' span={2}>{sn}</Item>
                <Item label='' span={1}>{name}</Item>
                <Item label='' span={2}>{this.runosdisplay(runos)}</Item>
                <Item label='' span={3}>{installlocation.name}</Item>
                <Item label='ip' span={3}>{ip}</Item>
                <Item label='mac' span={3}>{mac}</Item>
                <Item label='' span={1}>{this.statusdisplay(status)}</Item>
                <Item label='' span={2}>{shortDate(installdate)}</Item>
                <Item label='' span={3}>{remarks}</Item>
              </Descriptions>
            </Card>  
        )
    }
}