import React,{Component} from 'react'
import PropTypes from 'prop-types'
import {Upload,Icon,Modal,message} from 'antd'

import {BASE_IMG_URL,FILELIST_LENGTH} from '../../../../utils/constants'

export default class PicsWall extends Component{
    
    static proTypes={
        cover:PropTypes.array
    }
    
    constructor(props){
        super(props)
        let fileList=[]
        const {cover}=this.props
        if(cover&&cover.length>0){
            let imgarr=cover.split(',')
            fileList=imgarr.map((ia,index)=>({
                    uid:-index,
                    name:ia,
                    statur:'done',
                    url:BASE_IMG_URL+ia
                }
            ))
        }
        this.state={
            previewVisble:false,//
            previewImage:'', //
            fileList
        }
    }
    //
    getImgs=()=>{
        //return this.state.fileList.map(file=>file.name)
        let obj=''
        this.state.fileList.map(file=>
            obj+=file.name+','
        )
        return obj.substring(0,obj.length-1)
    }
    
    handleCancel=()=>this.setState({previewVisble:false})
    
    //
    handlePreview=file=>{
        this.setState({
            previewImage:file.url||file.thumbUrl,
            previewVisble:true,
        })
    }
    handleChange=({file,fileList})=>{
        if(file.status==='done'){
            const result=file.response
            if(result.status===1){
                message.success('!')
                const {data}=result
                file=fileList[fileList.length-1]
                file.name=data[0]
                file.url=data[2]
            }else{
                message.error('!')
            }
            
        }else if(file.status==='remove'){
            /*const result=await reqDeleteImg(file.name)
            if(result.status===0){
                message.success('!')
            }else{
                message.error('!')
            }*/
        }
        this.setState({fileList})
        }
    
    render(){
        const {previewImage,previewVisble,fileList}=this.state
        const uploadButton=(
            <div>
                <Icon type="plus"/>
                <div></div>
            </div>
        )
        return(
            <div>
                <Upload
                 action='/api/oapm/img/'
                 accept='image/*'
                 name='image'
                 listType='picture-card'
                 fileList={fileList}
                 onPreview={this.handlePreview}
                 onChange={this.handleChange}
                >
                {fileList.length>=FILELIST_LENGTH?null:uploadButton}
                </Upload>
                <Modal width='80%' visible={previewVisble} footer={null} onCancel={this.handleCancel}>
                    <img alt='' style={{width:'auto'}} src={previewImage}/>
                </Modal>
            </div>
        )
    }
}
