import React,{Component} from 'react'
import {connect} from 'react-redux'

import {Card,Form,Input,Select,Button,Icon,DatePicker,message} from 'antd'
import BackBtn from '../../../../components/backbtn'
import PicsWall from './picswall'
import RichTextEditor from './richtexteditor'

import {rPrs,couPr,rPcs} from '../../../../redux/actions/oapm-action'
import {rDeps,rUs} from '../../../../redux/actions/account-action'
import {shortDate} from '../../../../utils/dateUtils'
import {problemState,processingMode} from '../../../../config/selectConfig'
import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')

const Item=Form.Item
const {Option}=Select
const {TextArea}=Input
const thisDate=shortDate(Date.now())

class AddOrUpdate extends Component{
    state={
        departments:[],
        users:[],
        problemcategorys:[],
    }
    
    constructor(props){
        super(props)
        this.pw=React.createRef()
        this.editor=React.createRef()
    }
    
    initProblemCategorys=async ()=>{
        await this.props.rPcs()
        const result=this.props.problemCategoryReducer
        const problemcategorys=result.data
        this.setState({problemcategorys}) 
    }
    
    handleSearchDepartments=async value =>{
        if(value){
            await this.props.rDeps({'code':value})
            await this.props.rDeps({'name':value})
            const result=this.props.departmentReducer
            const departments=result.data
            this.setState({departments})            
        }else{
            this.setState({departments:[]})
        }
        
    }
    
    handleSearchUsers=async value =>{
        if(value){
            await this.props.rUs({'username':value})
            await this.props.rUs({'name':value})
            const result=this.props.userReducer
            const users=result.data
            this.setState({users})            
        }else{
            this.setState({users:[]})
        }
    }
    
    displayRender=(label)=> {
        return label[label.length - 1]
    }

    submit=()=>{
        this.props.form.validateFields(async(error,values)=>{
            if(!error){
                const {
                    create_time,
                    problem_state,
                    department,
                    discoverer,
                    problem_category,
                    processing_mode,
                    handler,
                    situation
                }=values
                const imgs=this.pw.current.getImgs()
                const solution=this.editor.current.getSolution()
                const seldep=[]
                seldep.push(department)
                const processedrecord={
                    create_time,
                    problem_state,
                    department:seldep,
                    discoverer,
                    problem_category,
                    processing_mode,
                    handler,
                    situation,
                    solution,
                    imgs
                }
                if(this.isUpdate){
                    processedrecord._id=this.processedrecord._id
                }
                await this.props.couPr(processedrecord)
                const result=this.props.processedRecordReducer
                if(result.status===1){
                    message.success(result.msg)
                    this.props.history.goBack()
                }else{
                    message.error(result.msg)
                }
            }
        })
    }
    
    componentWillMount(){
        this.initProblemCategorys()
        
        const processedrecord=this.props.location.state
        this.isUpdate=!!processedrecord
        //{} undifine
        this.processedrecord=processedrecord ||{}
    }
    render(){
        const {users,departments,problemcategorys}=this.state
        const departmentoptions = departments.map(d => <Option key={d._id} >{d.name}</Option>)
        const useroptions = users.map(d => <Option key={d._id} >{d.username}-{d.name}</Option>)
        const {isUpdate,processedrecord}=this
        const {
            create_time,
            problem_state,
            department,
            discoverer,
            problem_category,
            processing_mode,
            handler,
            situation,
            solution,
            imgs
        }=processedrecord
        const {getFieldDecorator}=this.props.form
        const formItemLayout={
            labelCol:{span:4},
            wrapperCol:{span:8}
        }

        const title=(
            <span>
                <BackBtn onClick={()=>this.props.history.goBack()}/>
                <span>{isUpdate ? '':''}</span>
            </span>
        )
        return(
            <Card title={title}>
                <Form {...formItemLayout}>
                    <Item label="">
                    {getFieldDecorator('create_time',{
                        initialValue:isUpdate ? moment(create_time,'YYYY-MM-DD') : moment(thisDate,'YYYY-MM-DD'),
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <DatePicker/>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('problem_state',{
                        initialValue:isUpdate ? problem_state.toString() :'2',
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select>
                        {
                            problemState.map(ps=><Option key={ps.value} value={ps.value}>{ps.label}</Option>)
                        }
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('department',{
                        initialValue:department? department[department.length-1]._id :2,
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select
                            showSearch
                            style={{ width: '100%' }}
                            placeholder=""
                            showArrow={false}
                            filterOption={false}
                            onSearch={this.handleSearchDepartments}
                            notFoundContent={null}
                        >
                        {departmentoptions}
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('discoverer',{
                        initialValue:discoverer ? discoverer._id : 1,
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select
                            showSearch
                            style={{ width: '100%' }}
                            placeholder=""
                            showArrow={false}
                            filterOption={false}
                            onSearch={this.handleSearchUsers}
                            notFoundContent={null}
                        >
                        {useroptions}
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('problem_category',{
                        initialValue:isUpdate ? problem_category._id : 2,
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select>
                        {
                            problemcategorys.map(pc=><Option key={pc._id} value={pc._id}>{pc.name}</Option>)
                        }
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('processing_mode',{
                        initialValue:isUpdate ? processing_mode.toString() : '3',
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select>
                        {
                            processingMode.map(pm=><Option key={pm.value} value={pm.value}>{pm.label}</Option>)
                        }
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('handler',{
                        initialValue:isUpdate ? handler._id : 1,
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <Select
                            showSearch
                            style={{ width: '100%' }}
                            placeholder=""
                            showArrow={false}
                            filterOption={false}
                            onSearch={this.handleSearchUsers}
                            notFoundContent={null}
                        >
                        {useroptions}
                        </Select>
                    )}
                    </Item>
                    <Item label="">
                    {getFieldDecorator('situation',{
                        initialValue:isUpdate ? situation : '',
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <TextArea placeholder='' autoSize={{minRows:2,maxRows:6}}/>
                    )}
                    </Item>
                    <Item label="" labelCol={{span:4}} wrapperCol={{span:20}}>
                    {getFieldDecorator('solution',{
                        initialValue:isUpdate ? solution : '--',
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <RichTextEditor ref={this.editor} solution={solution}/>
                    )}
                    </Item>
                    <Item label="">
                        <PicsWall ref={this.pw} imgs={imgs}/>
                    </Item>
                    <Item>
                        <Button type='primary' onClick={this.submit}><Icon type="save"/></Button>&nbsp;&nbsp;
                        <Button type='danger'><Icon type="close" /></Button>
                    </Item>
                </Form>
            </Card>
        )
    }
}
const mapStateToProps = state => {
    return {
        processedRecordReducer:state.processedRecordReducer,
        departmentReducer:state.departmentReducer,
        userReducer:state.userReducer,
        problemCategoryReducer:state.problemCategoryReducer
    }
}

const mapDispatchToProps = {rPrs,couPr,rPcs,rDeps,rUs}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Form.create()(AddOrUpdate))