import React,{PureComponent} from 'react';
import PropTypes from 'prop-types'

import {Form,Input,TreeSelect,Switch} from 'antd'

const Item=Form.Item

class AddForm extends PureComponent{

    static propTypes={
        setForm:PropTypes.func.isRequired,
        departments:PropTypes.array.isRequired,
        department:PropTypes.object
    }
    
    componentWillMount(){
        this.props.setForm(this.props.form)
    }
    
    render(){
        const {department}=this.props
        const {getFieldDecorator}=this.props.form
        const formItemLayout={
            labelCol:{span:5},
            wrapperCol:{span:15}
        }
        return(
            <Form>
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('name',{
                        initialValue:department.name,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <Input placeholder='!'/>
                    )
                }
                </Item>
                <Item label='' {...formItemLayout}>
                    {
                    getFieldDecorator('code',{
                        initialValue:department.code,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <Input placeholder='!'/>
                    )
                    }
                </Item>
                <Item label='' {...formItemLayout}>
                    {
                    getFieldDecorator('_parent',{
                        initialValue:department._parent
                    })(
                        <TreeSelect
                        style={{ width: '100%' }}
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                        treeData={this.props.departments}
                        placeholder=""
                        treeDefaultExpandAll
                        onChange={this.onChange}
                        />
                    )}
                </Item>
                <Item label='' {...formItemLayout}>
                    {
                    getFieldDecorator('status', { 
                        valuePropName: 'checked',
                        initialValue:department.status
                    })(
                        <Switch checkedChildren="" unCheckedChildren="" />
                    )}
                </Item>
            </Form>
        )
    }
}
export default Form.create()(AddForm)