/*
action creator
action: {type:'',data:}
action: dispatch=>{}
*/
import {
    GET_ZYKS,
    GET_MZKS,
    CHART_PROCESSEDRECORD_DAY,
    CHART_PROCESSEDRECORD,
    CHART_DEVICE,
    CHART_QYZLQK,
    CHART_BMYSZLQK,
    CHART_QYZDQK,
    CHART_BMYSZDQK,
} from '../actiontypes'

import {
    getzyks,
    getmzks,
    chart_processedrecord_day,
    chart_processedrecord,
    chart_device,
    chart_qyzlqk,
    chart_bmyszlqk,
    chart_qyzdqk,
    chart_bmyszdqk,
} from '../../api'


//
const rZyksAction = data =>({
    type:GET_ZYKS,
    data
})
export const rZyks = () => async dispatch => {
    const res = await getzyks()
    const departments=[]
    for (let d in res){
        departments.push({'_id':res[d][0],'name':res[d][1]})
    }
    dispatch(rZyksAction(departments))
  }

