import React,{Component} from 'react';
import PropTypes from 'prop-types'

import {Form,Input,DatePicker} from 'antd'
import {shortDate} from '../../../utils/dateUtils'
import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')
const Item=Form.Item
const {TextArea}=Input
const thisDate=shortDate(Date.now())

class AddForm extends Component{
    
    static propTypes={
        setForm:PropTypes.func.isRequired,
        timeline:PropTypes.object
    }
    
    componentWillMount(){
        this.props.setForm(this.props.form)
    }
    
    render(){
        const {timeline}=this.props
        const {getFieldDecorator}=this.props.form
        const formItemLayout={
            labelCol:{span:5},
            wrapperCol:{span:16}
        }
        return(
            <Form {...formItemLayout}>
                <Item label="">
                    {getFieldDecorator('create_time',{
                        initialValue: timeline.create_time?moment(timeline.create_time,'YYYY-MM-DD'):moment(thisDate,'YYYY-MM-DD'),
                        rules:[
                        {
                            required:true,message:'!'
                        }
                        ]
                    })(
                        <DatePicker/>
                    )}
                </Item>
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('name',{
                        initialValue:timeline.name,
                        rules:[
                        {required:true,message:'!'}
                        ]
                    })(
                        <TextArea placeholder='' autoSize={{minRows:2,maxRows:6}}/>
                    )
                }
                </Item>
                <Item label='' {...formItemLayout}>
                {
                    getFieldDecorator('details',{
                        initialValue:timeline.details,
                        rules:[
                        {required:false}
                        ]
                    })(
                        <TextArea placeholder='' autoSize={{minRows:2,maxRows:6}}/>
                    )
                }
                </Item>
            </Form>
        )
    }
}
export default Form.create()(AddForm)